.app__work {
	flex: 1;
	width: 100%;
	flex-direction: column;
}

.app__work-filter {
	display: flex;
	justify-content: center;
	align-items: center;

	margin: 3rem 0 2rem;

	.app__work-filter-item {
		padding: 0.5rem 0.6rem;
		margin: 0 0.5rem;

		font-weight: 800;
		background-color: var(--white-color);
		color: #000;
		border-radius: 5px;
		cursor: pointer;
		transition: 0.3s ease-in-out;

		&:hover {
			background: var(--secondary-color);
			color: var(--white-color);
		}

		@media screen and (min-width: 2000px) {
			padding: 1rem 2rem;
			border-radius: 0.85rem;
		}
	}
	.item-active {
		background: var(--secondary-color);
		color: var(--white-color);
	}
}

.app__work-portfolio {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: center;

	.app__work-item {
		width: 270px;
		flex-direction: column;
		border-radius: 0.5rem;
		margin: 2rem;
		padding: 1rem;
		background-color: #fff;
		color: #000;
		transition: all 0.3s ease-in-out;

		.app__work-img {
			width: 100%;
			height: 230px;
			position: relative;

			img {
				width: 100%;
				height: 100%;

				object-fit: cover;
				border-radius: 0.5rem;
			}

			@media screen and (min-width: 2000px) {
				height: 350px;
			}
		}

		&:hover {
		 box-shadow: 0 0 20px rgba(0, 0, 0, 0.5)
		}
	}
}

.app__work-hover {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.5);

	border-radius: 0.5rem;
	opacity: 0;
	transition: all 0.3s ease;

	div {
		width: 50px;
		height: 50px;
		border-radius: 50%;
		background-color: rgba(0, 0, 0, 0.5);
		color: #fff;

		margin: 1rem;
		font-family: var(--font-base);
		font-weight: 800;
		cursor: pointer;
		transition: all 0.3s ease;

		svg {
			width: 50%;
			height: 50%;
			color: var(--white-color);
		}
	}
}

.app__work-content {
	flex-direction: column;
	width: 100%;
	padding: 0.5rem;
	position: relative;

	h4 {
		margin-top: 0.6rem;
		line-height: 1.5;
	}

	.app__work-tag {
		position: absolute;
		padding: 0.5rem;
		border-radius: 10px;
		background-color: #fff;
		top: -25px;
	}
}
