#home {
	position: relative;
	background: url("../../assets/bgIMG.png");
	background-size: cover;
	background-repeat: repeat;
	background-position: center;

	.app__wrapper {
		padding: 0;

		.copyright {
			display: none;
		}
	}
}

.app__header {
	flex: 1;
	width: 100%;
	height: 100%;
	flex-direction: row;
	// border-bottom: 1px solid #0000001a;
	// box-shadow: 0 0px 20px rgba($color: #000000, $alpha: 0.1);

	padding: 7rem 2rem 0;

	@media screen and (min-width: 2000px) {
		padding-top: 8rem;
	}
	@media screen and (max-width: 1200px) {
		flex-direction: column;
	}
	@media screen and (max-width: 450px) {
		padding: 6rem 1rem 2rem;
	}
}

.app__header-info {
	flex: 0.65;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	height: 100%;

	margin: 0 2rem;

	@media screen and (max-width: 2000px) {
		width: 100%;
		margin-right: 0rem;
	}
}

.app__header-badge {
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: flex-end;

	.badge-cmp,
	.tag-cmp {
		padding: 1rem 2rem;
		width: auto;
		background: var(--white-color);
		box-shadow: 0 0 20px rgba($color: #000000, $alpha: 0.15);
		border-radius: 15px;
		flex-direction: row;
	}
	.badge-cmp {
		white-space: pre;
	}

	.tag-cmp {
		flex-direction: column;
		margin-top: 2rem;

		p {
			width: 100%;
			text-transform: uppercase;
			text-align: right;
		}
	}

	span {
		font-size: 2.5rem;

		@media screen and (min-width: 2000px) {
			font-size: 5rem;
		}
	}

	@media screen and (max-width: 1200px) {
		justify-content: flex-start;
		align-items: flex-start;
	}
}

.app__header-circles {
	flex: 0.75;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: flex-start;
	height: 100%;
	margin-left: 1rem;

	div {
		width: 100px;
		height: 100px;
		background-color: var(--white-color);
		border-radius: 50%;
		box-shadow: 0 0 20px rgba($color: #000000, $alpha: 0.15);

		img {
			width: 60%;
			height: 60%;
		}
	}

	div:nth-child(1) {
		width: 150px;
		height: 150px;
	}
	div:nth-child(2) {
		margin: 1.75rem;
		width: 80px;
		height: 80px;
	}
	div:nth-child(3) {
		width: 120px;
		height: 120px;
	}

	@media screen and (min-width: 2000px) {
		div:nth-child(1) {
			width: 400px;
			height: 400px;
		}
		div:nth-child(2) {
			margin: 1.75rem;
			width: 150px;
			height: 150px;
		}
		div:nth-child(3) {
			width: 320px;
			height: 320px;
		}
	}

	@media screen and (max-width: 1200px) {
		width: 100%;
		flex-direction: row;
		flex-wrap: wrap;
		margin-left: 0;

		div {
			margin: 1rem;
		}
	}
}

.app__header-img {
	flex: 2;
	height: 100%;

	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
	position: relative;

	margin-left: 1rem;

	//Added to increase width per screen size
	img {
		width: 110%;
		object-fit: contain;
		z-index: 1;

		@media screen and (min-width: 450px) {
			width: 120%;
		}
	}

	.overlay_circle {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 0;
		width: 100%;
		height: 95%;
	}

	@media screen and (max-width: 1200px) {
		margin: 2rem 0;
	}
}
